body {
  margin: 0;
  font-family: 'Inter', Arial, sans-serif;
  background: #181e23;
  color: #cfd8dc;
}

.dashboard-container {
  display: flex;
  height: 100vh;
  background: #181e23;
}

.sidebar {
  width: 220px;
  background: #22272c;
  padding: 24px 0 0 0;
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.profile-section {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 32px;
  padding-left: 32px;
}

.profile-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #00bcd4;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 1.5rem;
}

.profile-name {
  font-weight: bold;
  font-size: 1.1rem;
  color: #fff;
}

.profile-role {
  font-size: 0.9rem;
  color: #8fa1b3;
}

.sidebar nav ul {
  list-style: none;
  padding: 0;
}

.sidebar nav ul li {
  padding: 12px 32px;
  color: #cfd8dc;
  cursor: pointer;
  transition: background 0.2s;
}

.sidebar nav ul li.active,
.sidebar nav ul li:hover {
  background: #2c3136;
  color: #fff;
}

.main-content {
  flex: 1;
  padding: 0 32px;
  display: flex;
  flex-direction: column;
}

.header {
  margin-top: 24px;
  margin-bottom: 24px;
}

.back {
  color: #8fa1b3;
  font-size: 0.9rem;
  cursor: pointer;
}

.title-section {
  display: flex;
  align-items: baseline;
  gap: 24px;
  margin: 12px 0;
}

.title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #fff;
}

.refresh {
  font-size: 0.9rem;
  color: #8fa1b3;
}

.tabs {
  display: flex;
  gap: 32px;
  margin-top: 16px;
}

.tab {
  padding-bottom: 8px;
  font-size: 1rem;
  color: #8fa1b3;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: color 0.2s, border-bottom 0.2s;
}

.tab.active {
  color: #fff;
  border-bottom: 2px solid #00bcd4;
}

.panels {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.panel-row {
  display: flex;
  gap: 24px;
}

.panel {
  background: #23282e;
  border-radius: 8px;
  padding: 40px;
  color: #8fa1b3;
  flex: 1;
  min-height: 120px;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
